import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

export enum ButtonState {
  primary, secondary, success, warn, danger
}


@Component({
  selector: 'app-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss']
})
export class ButtonComponent implements OnInit {

  ButtonState = ButtonState;
  @Input() name: any;
  @Input() type: String|null = null;  //only used for attribute input
  @Input('disabled') isDisabled : boolean = false;
  state: ButtonState = ButtonState.primary;

  ngOnInit() {
    if (this.type === "success") {
      this.state = ButtonState.success;
    } else if (this.type === "warn") {
      this.state = ButtonState.warn;
    } else if (this.type === "danger") {
      this.state = ButtonState.danger;
    } else if (this.type === "secondary") {
      this.state = ButtonState.secondary;
    } else {
      this.state = ButtonState.primary;
    }
  }

  onClick() {
  }

}
