import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {HeaderComponent} from './components/header/header.component';
import {FooterComponent} from './components/footer/footer.component';
import {HomeComponent} from './components/home/home.component';
import {LoginComponent} from './components/login/login.component';
import {MessageComponent} from './components/message/message.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {httpInterceptorProviders} from './interceptors';
import { UiGuideComponent } from './ui-guide/ui-guide.component';
import { ModalComponent } from './components/basic/modal/modal.component';
import { ButtonComponent } from './components/basic/button/button.component';
import { DatepickerComponent } from './components/basic/datepicker/datepicker.component';
import { TextinputComponent } from './components/basic/textinput/textinput.component';
import { SelectinputComponent } from './components/basic/selectinput/selectinput.component';
import { OptionDirective } from './directives/select/option.directive';
import { CheckboxComponent } from './components/basic/checkbox/checkbox.component';
import { ExerciseCreateComponent } from './components/exercise/exercise-create/exercise-create.component';
import { ExerciseListComponent } from './components/exercise/exercise-list/exercise-list.component';
import { WorkoutCreateComponent} from "./components/workout/workout-create/workout-create.component";

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    LoginComponent,
    MessageComponent,
    UiGuideComponent,
    ModalComponent,
    ButtonComponent,
    DatepickerComponent,
    TextinputComponent,
    SelectinputComponent,
    OptionDirective,
    CheckboxComponent,
    ExerciseCreateComponent,
    ExerciseListComponent,
    WorkoutCreateComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgbModule,
    FormsModule,
  ],
  providers: [httpInterceptorProviders],
  bootstrap: [AppComponent]
})
export class AppModule {
}
