import { AfterContentInit, Component, ContentChild, ContentChildren, Directive, ElementRef, Input, OnInit, QueryList, Renderer2, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { uniqueId } from 'lodash';
import { OptionDirective } from 'src/app/directives/select/option.directive';


@Component({
  selector: 'app-selectinput',
  templateUrl: './selectinput.component.html',
  styleUrls: ['./selectinput.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectinputComponent),
      multi: true,
    },
  ],
})
export class SelectinputComponent implements AfterContentInit, ControlValueAccessor {
  
  selectInputId: string = uniqueId('select-input-');

  showSelect: boolean = false;
  @Input('disabled') isDisabled: boolean = false;
  @Input() name: string = this.selectInputId;
  @Input() placeholder: string = null;
  @Input() search: string = null;
  @Input() label: string = null;
  @Input() state: 'primary' | 'secondary' | 'success' | 'warn' | 'error' = 'primary';
  @Input() forceNoLabel: string;

  @ContentChildren(OptionDirective) optionDirectives: QueryList<OptionDirective>;
  selectedOption: OptionDirective = null;
  selectOptionContent: string;
  searchString: string = "";
  removeListener: () => void;
  filteredOptions: OptionDirective[];
  onChange: any = () => {};
  onTouched: any = () => {};

  constructor(private el: ElementRef, private renderer: Renderer2) { }
  writeValue(val: string): void {
    let o: OptionDirective|null = null;
    
    this.optionDirectives.toArray().forEach(option => {
      if (val === option.value) this.selectOption(option); 
    });
    
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  
  ngAfterContentInit(): void {
    this.filterOptions();
  }
  
  ngOnInit(): void {
  }
  
  toggleSelect() {
    if (this.isDisabled || this.showSelect) {
      this.closeSelect();
    } else {
      this.openSelect();
    }
  }

  openSelect() {
    this.showSelect = true;
    this.removeListener = this.renderer.listen(
      'document',
      'click',
      (event) => {
        this.handleGlobalClick(event);
      }
    );
  }
  handleGlobalClick(event: any) {
    const clickedInside = this.el.nativeElement.contains(event.target);

    if (!clickedInside) {
      this.closeSelect();
    }
  }

  closeSelect() {
    this.showSelect = false;
    if (this.removeListener != null) {
      this.removeListener();
      this.removeListener = null;
    }
  }

  selectOption(value: OptionDirective) {

    if (!this.isDisabled) {
      this.selectOptionContent = value != null ? value.content : null;
      this.onChange(value != null ? value.value : null);
      this.onTouched();
  
      setTimeout(() => {
        this.closeSelect();
        this.selectedOption = value;
      }, 150);
    }
  }

  filterOptions() {
    const filteredOptions = this.optionDirectives.toArray().filter(option => {
      // Add your filtering logic here, for example, case-insensitive search
      return option.value.toLowerCase().includes(this.searchString.toLowerCase()) ||
        option.content.toLowerCase().includes(this.searchString.toLowerCase());
    });

    console.log(this.searchString);

    this.filteredOptions = filteredOptions;
  }

  setState(state: typeof this.state) {
    this.state = state;
  }

}
