import { Component, ElementRef, Input, Renderer2, ViewChild, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { uniqueId } from 'lodash';

@Component({
  selector: 'app-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DatepickerComponent),
      multi: true,
    },
  ],
})
export class DatepickerComponent implements ControlValueAccessor {

  // CONSTANTS
  readonly monthNames: string[] = [
    'Januar', 'Februar', 'Mrz', 'April',
    'Mai', 'Juni', 'Juli', 'August',
    'September', 'Oktober', 'November', 'Dezember'
  ];
  readonly dayNames: string[] = ['So', 'Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa'];

  // DATEPICKER
  datepickerInputId = uniqueId("datepicker-");  // unique id for the input element
  showDatepicker: boolean = false;              // indicates if datepicker is shown
  showYearpicker: boolean = false;              // indicates if yearpicker is shown
  datepickerValue!: string;                     // the textvalue, that is shown in the input
  datepickerMonth!: number;                     // the current month, selected in the datepicker-element
  datepickerYear!: number;                      // the current year, selected in the datepicker-element
  yearPickerMaxDate: number;                    // range maximum for yearpicker
  yearPickerMinDate: number;                    // range minimum for yearpicker
  removeListener: () => void | null = null;     // function to remove the clicklistener (is set on "listen")

  selectedDate!: Date | null;

  // INPUTS
  @Input() state: 'primary' | 'secondary' | 'success' | 'warn' | 'error' = 'primary';
  @Input() label: String | null = null;
  @Input() placeholder: String | null = null;
  @Input('disabled') isDisabled: boolean = false;
  @Input() forceNoLabel: string;

  // CONTROL VALUE ACCESSOR
  onChange: any = () => { };
  onTouched: any = () => { };

  constructor(private el: ElementRef, private renderer: Renderer2) { }

  ngOnInit(): void {
    let today = new Date();
    this.datepickerMonth = today.getMonth();
    this.datepickerYear = today.getFullYear();

    this.yearPickerMaxDate = this.datepickerYear + 11;
    this.yearPickerMinDate = this.datepickerYear - 12;
  }

  // Override
  writeValue(date: Date): void {
    this.selectedDate = date;
    if (date != null) {
      this.datepickerMonth = date.getMonth();
      this.datepickerYear = date.getFullYear();
    }
  }

  // Override
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  // Override
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  // Override
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  setDate(date: Date) {
    this.selectedDate = date;
    this.onChange(this.selectedDate);
    this.onTouched();
    if(date != null) {
      const day = this.padZero(this.selectedDate.getDate());
      const month = this.padZero(this.selectedDate.getMonth() + 1); // Months are zero-based
      const year = this.selectedDate.getFullYear();
  
      this.datepickerMonth = this.selectedDate.getMonth();
      this.datepickerYear = this.selectedDate.getFullYear();
  
      this.datepickerValue = `${day}.${month}.${year}`;
    } else {
      this.datepickerValue = null;
    }
  }

  toggleDatepicker() {
    if (this.isDisabled) {
      this.showDatepicker = false;
      this.showYearpicker = false;
    } else {
      this.showDatepicker = !(this.showDatepicker || this.showYearpicker);
      this.showYearpicker = false;

      if (this.showDatepicker && !this.removeListener) {
        //the "removeListener" function is returned here. A bit weird but we save it and call it in""removeGlobalClickListener".
        this.removeListener = this.renderer.listen(
          'document',
          'click',
          (event) => {
            this.handleGlobalClick(event);
          }
        );
      } else {
        this.removeGlobalClickListener();
      }
    }
  }

  onTextInputChanged(event: any) {

    console.log('Event triggered!');
    let date: Date | null = this.parseDate(event.target.value);

    this.setDate(date);
  }

  parseDate(dateString: string): Date | null {
    console.log('Hello From ParseDate');
    if (dateString === '') {
      return null;
    }
    const [day, month, year] = dateString.includes('-') || dateString.includes('.') || dateString.includes('/')
      ? dateString.split(/[\/.-]+/)
      : /^\d{8}$/.test(dateString) ?  [dateString.substring(0, 2), dateString.substring(2, 4), dateString.substring(4)] : [null, null, null];

    if (day && month && year) {
      const parsedDate = new Date(Number(year), Number(month) - 1, Number(day));

      if (!isNaN(parsedDate.getTime())) {
        return parsedDate;
      }
    }

    return null;
  }

  handleGlobalClick(event: any) {
    const clickedInside = this.el.nativeElement.contains(event.target);

    if (!clickedInside) {
      this.showDatepicker = false;
      this.showYearpicker = false;
      this.removeGlobalClickListener();
    }
  }

  removeGlobalClickListener() {
    if (this.removeListener != null) {
      this.removeListener();
      this.removeListener = null;
    }
  }

  pickDate() {
    this.showDatepicker = true;
    this.showYearpicker = false;
  }

  pickYear() {
    if (this.isDisabled) {
      this.showDatepicker = false;
      this.showYearpicker = false;
      return;
    }
    this.yearPickerMaxDate = this.datepickerYear + 11;
    this.yearPickerMinDate = this.datepickerYear - 12;
    this.showDatepicker = false;
    this.showYearpicker = true;
  }

  selectDate(date: any) {
    this.selectedDate = new Date(this.datepickerYear, this.datepickerMonth, date);
    this.onChange(this.selectedDate);
    this.onTouched();
    const day = this.padZero(this.selectedDate.getDate());
    const month = this.padZero(this.selectedDate.getMonth() + 1); // Months are zero-based
    const year = this.selectedDate.getFullYear();

    this.datepickerValue = `${day}.${month}.${year}`;
  }

  padZero(num: number): string {
    return num < 10 ? `0${num}` : `${num}`;
  }

  getBlankDays(): number[] {
    let dayOfWeek = new Date(this.datepickerYear, this.datepickerMonth).getDay();
    let blankdaysArray = [];
    for (var i = 1; i <= dayOfWeek; i++) {
      blankdaysArray.push(i);
    }
    return blankdaysArray;
  }

  getDays(): number[] {
    const daysInMonth = new Date(this.datepickerYear, this.datepickerMonth + 1, 0).getDate();
    let daysArray = [];
    for (var i = 1; i <= daysInMonth; i++) {
      daysArray.push(i);
    }
    return daysArray;
  }

  getYearsRange(start: number, end: number): number[] {
    const years: number[] = [];
    for (let i = start; i <= end; i++) {
      years.push(i);
    }
    return years;
  }

  changeMonth(offset: number) {
    const totalMonths = this.datepickerYear * 12 + this.datepickerMonth + offset;
    this.datepickerYear = Math.floor(totalMonths / 12);
    this.datepickerMonth = this.mod(totalMonths, 12);
  }

  // % is not modulo in javascript. Thank you javascript.
  mod(n: number, m: number): number {
    return ((n % m) + m) % m;
  }

  isToday(date: any) {
    const today = new Date();
    const d = new Date(this.datepickerYear, this.datepickerMonth, date);
    return today.toDateString() === d.toDateString() ? true : false;
  }

  isSelected(date: any) {
    return new Date(this.datepickerYear, this.datepickerMonth, date).toDateString() === (this.selectedDate != null ? this.selectedDate.toDateString() : "");
  }

  changeState(state: typeof this.state) {
    this.state = state;
  }

}
