import { Component, OnInit, ViewChild } from '@angular/core';
import { muscleGroup } from 'src/app/enums/muscleGroup';
import { equipment } from 'src/app/enums/equipment';
import { ExerciseCreateDto, ExerciseDetailDto } from 'src/app/dtos/exercise';
import { FormBuilder, FormControl, FormGroup, FormsModule, Validators } from '@angular/forms';
import { SelectinputComponent } from '../../basic/selectinput/selectinput.component';
import { ExerciseService } from 'src/app/services/exercise.service';
import { ActivatedRoute, Router } from '@angular/router';


export enum ExerciseCreateEditMode {
  create = "Create",
  edit = "Edit",
}

@Component({
  selector: 'app-exercise-create',
  templateUrl: './exercise-create.component.html',
  styleUrls: ['./exercise-create.component.scss']
})
export class ExerciseCreateComponent implements OnInit {
  muscleGroupOption = Object.values(muscleGroup).map(item => String(item));
  equipmentOption = Object.values(equipment).map(item => String(item));
  exerciseForm: FormGroup;
  exerciseId: number = 0;
  mode: ExerciseCreateEditMode = ExerciseCreateEditMode.create;

  ngOnInit(): void {
    this.buildForm('', '', undefined, undefined, 20);

    this.route.data.subscribe(data => {
      this.mode = data.mode; // Edit or Create
    });

    if (this.mode === ExerciseCreateEditMode.edit) {
      this.loadEditExercise();
    }
  }

  constructor(
    private exerciseServise: ExerciseService, 
    private router: Router, 
    private formBuilder: FormBuilder,
    private route: ActivatedRoute
  ) {}


  public submit(source: string) {
    if (this.mode === ExerciseCreateEditMode.create) {
      let ExerciseObj: ExerciseCreateDto = new ExerciseCreateDto(this.exerciseForm);
      this.exerciseServise.create(ExerciseObj).subscribe({
        next: () => {
          console.log('Successfully created Exercise.');
          this.router.navigate(['/exercises']);
        },
        error: error => {
          console.log('Could not create Exercise');
          console.log(error);
        }
      });

    } else {
      let ExerciseObj: ExerciseDetailDto = new ExerciseDetailDto(this.exerciseForm, this.exerciseId);
      this.exerciseServise.update(ExerciseObj).subscribe({
        next: () => {
          console.log('Successfully created Exercise.');
          this.router.navigate(['/exercises']);
        },
        error: error => {
          console.log('Could not create Exercise');
          console.log(error);
        }
      });
    }
  }

  public debug() {
    console.log(this.exerciseForm);
  }

  public loadEditExercise() {
    let id: number;
    let res: ExerciseDetailDto;

    this.route.params.subscribe(params => {
      id = params['id'].valueOf();
      this.exerciseId = id;
    });
    
    this.exerciseServise.getById(id).subscribe({
      next: data => {
        res = <ExerciseDetailDto> data;
        this.buildForm(res.title, res.description, res.muscleGroup, res.equipment, <number> res.baseRestDuration);
      },
      error: error => {
        console.log(error);
        this.router.navigate(['/exercises']);
      }
    });
  }

  private buildForm(title: string, description: string, muscleGroupOpt: string, equipmentOpt: string, restDuration: number) {
    this.exerciseForm = undefined;
    this.exerciseForm = this.formBuilder.group({
      title:[title, [Validators.required, Validators.maxLength(50)]],
      description: [description, [Validators.required, Validators.maxLength(512)]],
      muscleGroup:[muscleGroup[muscleGroupOpt], Validators.required],
      equipment:[equipment[equipmentOpt], Validators.required],
      restMin:[Math.trunc((restDuration) / 60), [Validators.min(0), Validators.max(10)]],
      restSec:[(restDuration) % 60, [Validators.min(0), Validators.max(59)]]
    });
  }
}
