import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Globals } from '../global/globals';
import { ExerciseCreateDto, ExerciseDetailDto } from '../dtos/exercise'
import { Observable, tap } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ExerciseService {
  private exerciseBaseUri: string = this.globals.backendUri + '/exercises';

  constructor(private httpClient: HttpClient, private globals: Globals) {
  }

  create(exercise:  ExerciseCreateDto): Observable<ExerciseDetailDto> {
    console.log('Create message with title ' + exercise.title);
    return this.httpClient.post<ExerciseDetailDto>(this.exerciseBaseUri, exercise)
    .pipe(tap(h => this.mapExercise(h)));
  }

  update(exercise:  ExerciseDetailDto): Observable<ExerciseDetailDto> {
    console.log('Update message with title ' + exercise.title);
    return this.httpClient.put<ExerciseDetailDto>(this.exerciseBaseUri, exercise)
    .pipe(tap(h => this.mapExercise(h)));
  }

  deleteById(id: number): Observable<void> {
    console.log('Delete message with id: ' + id);
    return this.httpClient.delete<void>(this.exerciseBaseUri + '/' + id);
  }

  getById(id:  number): Observable<ExerciseDetailDto> {
    console.log('Get message with id: ' + id);
    return this.httpClient.get<ExerciseDetailDto>(this.exerciseBaseUri + '/' + id)
    .pipe(tap(h => this.mapExercise(h)));
  }

  getAllExercises(): Observable<ExerciseDetailDto[]> {
    console.log('Get all exercises for the user');
    return this.httpClient.get<ExerciseDetailDto[]>(this.exerciseBaseUri)
    .pipe(tap(exercises => exercises.map(h => this.mapExercise(h))));
  }


  //Mapper
  private mapExercise(exercise: ExerciseDetailDto): ExerciseDetailDto {
    exercise.baseRestDuration = this.parseISO8601Duration(exercise.baseRestDuration);
    return exercise;
  }

  

  private parseISO8601Duration(iso8601Duration: string | number): number{ 
    if (typeof iso8601Duration  === 'number') {
      return iso8601Duration;
    }
    iso8601Duration = <string> iso8601Duration;
    let iso8601DurationRegex: RegExp = /(-)?P(?:([.,\d]+)Y)?(?:([.,\d]+)M)?(?:([.,\d]+)W)?(?:([.,\d]+)D)?T(?:([.,\d]+)H)?(?:([.,\d]+)M)?(?:([.,\d]+)S)?/;  
    const matches = iso8601Duration.match(iso8601DurationRegex);
    let duration = {
        sign: matches[1] === undefined ? '+' : '-',
        years: matches[2] === undefined ? 0 : parseInt(matches[2], 10),
        months: matches[3] === undefined ? 0 : parseInt(matches[3], 10),
        weeks: matches[4] === undefined ? 0 : parseInt(matches[4], 10),
        days: matches[5] === undefined ? 0 : parseInt(matches[5], 10),
        hours: matches[6] === undefined ? 0 : parseInt(matches[6], 10),
        minutes: matches[7] === undefined ? 0 : parseInt(matches[7], 10),
        seconds: matches[8] === undefined ? 0 : parseInt(matches[8], 10),
    };

      let totalSeconds: number = duration.seconds + duration.minutes * 60 + duration.hours * 3600 
      + duration.days * 86400 + duration.weeks * 604800 + duration.months * 2629746 + duration.years * 31556952;

      return totalSeconds;
  }
}
