import { Component, ViewChild } from '@angular/core';
import { DatepickerComponent } from '../components/basic/datepicker/datepicker.component';

@Component({
  selector: 'app-ui-guide',
  templateUrl: './ui-guide.component.html',
  styleUrls: ['./ui-guide.component.scss']
})
export class UiGuideComponent {
  @ViewChild('myCustomDatePicker', {static: false}) myCustomDatePicker : DatepickerComponent;

  isModalOpen: boolean = false;
  isSmModalOpen: boolean = false;
  isMdModalOpen: boolean = false;
  isLgModalOpen: boolean = false;

  myCustomDate: Date = null;

  printDate() {
    if (!this.myCustomDate) {
      this.myCustomDatePicker.changeState('error');
    } else {
      this.myCustomDatePicker.changeState('success');
    }
    
    console.log(this.myCustomDate);
  }

  closeModal() {
    this.isModalOpen = false;
  }

  openModal() {
    this.isModalOpen = true;
  }

  closeSmModal() {
    this.isSmModalOpen = false;
  }

  openSmModal() {
    this.isSmModalOpen = true;
  }

  closeMdModal() {
    this.isMdModalOpen = false;
  }

  openMdModal() {
    this.isMdModalOpen = true;
  }

  closeLgModal() {
    this.isLgModalOpen = false;
  }

  openLgModal() {
    this.isLgModalOpen = true;
  }

}
