import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Globals } from '../global/globals';
import { WorkoutCreateDto, WorkoutDetailDto } from '../dtos/workout';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class WorkoutService {
  private workoutBaseUri: string = this.globals.backendUri + '/workouts';

  constructor(private httpClient: HttpClient, private globals: Globals) {
  }

  create(workout: WorkoutCreateDto): Observable<WorkoutDetailDto> {
    console.log('Create workout with name ' + workout.name);
    return this.httpClient.post<WorkoutDetailDto>(this.workoutBaseUri, workout);
  }

  update(workoutId: number, workout: WorkoutCreateDto): Observable<WorkoutDetailDto> {
    console.log('Update workout with id ' + workoutId);
    return this.httpClient.put<WorkoutDetailDto>(`${this.workoutBaseUri}/${workoutId}`, workout);
  }

  deleteById(id: number): Observable<void> {
    console.log('Delete workout with id: ' + id);
    return this.httpClient.delete<void>(`${this.workoutBaseUri}/${id}`);
  }

  getById(id: number): Observable<WorkoutDetailDto> {
    console.log('Get workout with id: ' + id);
    return this.httpClient.get<WorkoutDetailDto>(`${this.workoutBaseUri}/${id}`);
  }
}
