import { Component, OnInit } from '@angular/core';
import { WorkoutService} from "../../../services/workout.service";
import {ExerciseService} from "../../../services/exercise.service";

@Component({
  selector: 'app-workout-create',
  templateUrl: './workout-create.component.html',
  styleUrls: ['./workout-create.component.scss']
})
export class WorkoutCreateComponent implements OnInit {

  selectedExerciseId: number;
  workoutCreateObj = {
    name: '',
    exerciseIds: [] as number[]
  };
  exerciseOptions: any[]; // Assuming this is populated with available exercises

  constructor(
    private workoutService: WorkoutService, // Adjust with your actual service
    private exerciseService: ExerciseService // Adjust with your actual service
  ) {}

  ngOnInit() {
    this.loadExercises();
  }

  loadExercises() {
    this.exerciseService.getAllExercises().subscribe(
      (exercises) => {
        this.exerciseOptions = exercises;
      },
      (error) => {
        console.error('Error fetching exercises', error);
      }
    );
  }

  addExercise(exerciseId: number) {
    if (!this.workoutCreateObj.exerciseIds.includes(exerciseId)) {
      this.workoutCreateObj.exerciseIds.push(exerciseId);
    }
  }

  submitWorkout() {
    this.workoutService.create(this.workoutCreateObj).subscribe(
      (response) => {
        console.log('Workout created successfully', response);
        // Redirect or clear form here as needed
      },
      (error) => {
        console.error('Error creating workout', error);
      }
    );
  }
}
