import { Component, OnInit } from '@angular/core';
import { ExerciseDetailDto } from 'src/app/dtos/exercise';
import { equipment } from 'src/app/enums/equipment';
import { muscleGroup } from 'src/app/enums/muscleGroup';
import { ExerciseService } from 'src/app/services/exercise.service';

@Component({
  selector: 'app-exercise-list',
  templateUrl: './exercise-list.component.html',
  styleUrls: ['./exercise-list.component.scss']
})
export class ExerciseListComponent implements OnInit{
  isLgModalOpen: boolean = false;
  exercises: ExerciseDetailDto[] = [new ExerciseDetailDto()];
  modalExercise: ExerciseDetailDto = new ExerciseDetailDto;
  modalDetailsLoaded: boolean = false;

  public constructor(private exerciseServise: ExerciseService) {}

  ngOnInit(): void {
    this.loadExercises();
  }

  public loadExercises() {
    this.exerciseServise.getAllExercises().subscribe({
      next: data => {
        this.exercises = <ExerciseDetailDto[]> data;
      },
      error: error => {
        console.log(error);
      }
    });
  }

  public getEnumString(enumVal: muscleGroup | equipment): string {
    if(muscleGroup[enumVal] === undefined) {
      return equipment[enumVal];
    } else {
        return muscleGroup[enumVal];
    }
  }

  public viewDetails(id : number) {
    this.exerciseServise.getById(id).subscribe({
      next: data => {
        this.modalExercise = <ExerciseDetailDto> data;
        this.modalDetailsLoaded = true;    
      },
      error: error => {
        console.log(error);
      }
    });
  }

  public deleteExercise(id: number) {
    this.exerciseServise.deleteById(id).subscribe({
      next: () => {
        console.log('Deleted the Exercise with id: ' + id);
        this.loadExercises();
        this.closeLgModal();
      },
      error: e => {
        console.log(e);
      }
    });
  }

  closeLgModal() {
    this.isLgModalOpen = false;
    this.modalDetailsLoaded = false;
  }

  openLgModal(id: number) {
    this.viewDetails(id);
    this.isLgModalOpen = true;
  }

  isUserExercise(): boolean {
    //todo
    return true;
  }

}
