import {NgModule} from '@angular/core';
import {mapToCanActivate, RouterModule, Routes} from '@angular/router';
import {HomeComponent} from './components/home/home.component';
import {LoginComponent} from './components/login/login.component';
import {AuthGuard} from './guards/auth.guard';
import {MessageComponent} from './components/message/message.component';
import { UiGuideComponent } from './ui-guide/ui-guide.component';
import { ExerciseCreateComponent, ExerciseCreateEditMode } from './components/exercise/exercise-create/exercise-create.component';
import { ExerciseListComponent } from './components/exercise/exercise-list/exercise-list.component';
import {WorkoutCreateComponent} from "./components/workout/workout-create/workout-create.component";

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'ui-guide', component: UiGuideComponent },
  { path: 'exercises', children: [
    {path: '',  component: ExerciseListComponent },
    {path: 'create', component: ExerciseCreateComponent,  data: {mode: ExerciseCreateEditMode.create}},
    {path: 'edit/:id', component: ExerciseCreateComponent, data: {mode: ExerciseCreateEditMode.edit}},
  ]},
  { path: 'create-workouts', component: WorkoutCreateComponent},
  {path: 'message', canActivate: mapToCanActivate([AuthGuard]), component: MessageComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
