import { Component, Input, forwardRef } from '@angular/core';
import { AbstractControl, ControlValueAccessor, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';
import { uniqueId } from 'lodash';

@Component({
  selector: 'app-textinput',
  templateUrl: './textinput.component.html',
  styleUrls: ['./textinput.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TextinputComponent),
      multi: true,
    },
  ],
})
export class TextinputComponent implements ControlValueAccessor {
  
  onInputChange() {
    this.onChange(this.value);
    this.onTouched();
  }
  
  inputId: string = uniqueId("text-input-");
  value: string = '';
  
  @Input() state: 'primary' | 'secondary' | 'success' | 'warn' | 'error' = 'primary';
  @Input('disabled') isDisabled: boolean = false;
  @Input() name: string = this.inputId;
  @Input() placeholder: string|null = null;
  @Input() label: string|null = null;
  @Input('type') type: string = 'text';
  @Input() forceNoLabel: string;
  @Input() textarea: string = null;
  @Input() message: string = null;
  
  // CONTROL VALUE ACCESSOR
  onChange: any = () => { };
  onTouched: any = () => { };


  writeValue(val: string): void {
    this.value = val;
  }
  
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  
  registerOnTouched(fn: any): void {
    this.onTouched = fn
  }
  
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  setState(state : typeof this.state) {
    this.state = state;
  }


}
