import { FormGroup } from "@angular/forms";
import { equipment } from "../enums/equipment";
import { muscleGroup } from "../enums/muscleGroup";
import { eq } from "lodash";

export class ExerciseCreateDto {
    title: string;
    description: string;
    muscleGroup: string;
    equipment: string;
    hasWeight: boolean;
    hasPlates: boolean;
    progressionFactor: number;
    baseRestDuration: number;
    avgRepDuration: number;

    public constructor(form: FormGroup) {
        let restDuration: number = form.get('restMin')?.value * 60;
        restDuration = restDuration + form.get('restSec').value;

        let muscleGroupVal: string = Object.keys(muscleGroup)[Object.values(muscleGroup).indexOf(form.get('muscleGroup').value)];
        let equipmentVal: string = Object.keys(equipment)[Object.values(equipment).indexOf(form.get('equipment').value)];
        return {
            title: form.get('title').value,
            description: form.get('description').value,
            muscleGroup: muscleGroupVal,
            equipment: equipmentVal,
            hasWeight: form.get('equipment').value === equipment.BODY_WEIGHT? false : true,
            hasPlates: form.get('title').value === equipment.FREE_WEIGHT_PLATES ? true : false,
            progressionFactor: 0,
            baseRestDuration: restDuration,
            avgRepDuration: 0,
        };
    }
}

export class ExerciseDetailDto {
    id: number;
    title: string;
    description: string;
    muscleGroup: string;
    equipment: string;
    hasWeight: boolean;
    hasPlates: boolean;
    progressionFactor: number;
    baseRestDuration: number | string;
    avgRepDuration: number;
    owner: string;

    constructor();
    constructor(form: FormGroup, id: number);
    constructor(form?: FormGroup, id?: number) {
        if (form && id) {
            let restDuration: number = form.get('restMin')?.value * 60;
            restDuration = restDuration + form.get('restSec').value;

            let muscleGroupVal: string = Object.keys(muscleGroup)[Object.values(muscleGroup).indexOf(form.get('muscleGroup').value)];
            let equipmentVal: string = Object.keys(equipment)[Object.values(equipment).indexOf(form.get('equipment').value)];
            return {
                id: id,
                title: form.get('title').value,
                description: form.get('description').value,
                muscleGroup: muscleGroupVal,
                equipment: equipmentVal,
                hasWeight: form.get('equipment').value === equipment.BODY_WEIGHT? false : true,
                hasPlates: form.get('title').value === equipment.FREE_WEIGHT_PLATES ? true : false,
                progressionFactor: 0,
                baseRestDuration: restDuration,
                avgRepDuration: 0,
                owner: null
            };
        } else {
            return {
                id: 0,
                title: '',
                description: '',
                muscleGroup: undefined,
                equipment: undefined,
                hasWeight: false,
                hasPlates: false,
                progressionFactor: 0,
                baseRestDuration: 0,
                avgRepDuration: 0,
                owner: null
            };
        }

    }
}
